import React, { useEffect, useState } from 'react';
import {useWindowScroll} from "react-use";



const Scroll = ():any => {
    const {y: YAxis} = useWindowScroll();
    const [existence, setExistence] = useState(false);
    const scrollToTop = () => window.scrollTo({top:0, behavior:"auto"})

    useEffect(()=> {
        if(YAxis > 275){setExistence(true);}
        else{setExistence(false);}
    }, [[YAxis]]);

    if(!existence){return false;}
    else{
        return(
            <>
                
                   <div className = "bottom-button" onClick={scrollToTop} style = {{ cursor:'pointer'}}>&#8679;</div>

            </>
            
        );
    }
}
export default Scroll;